<template>
  <div class="diamond">
    <skeleton v-if="skeletonLoading" :nums="3"></skeleton>
    <div v-else>
      <div class="diamod-banner" :class="{'diamod-center': !linkInfo}" >
        <img class="bg-img" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/banner_bg.png"/>
        <div class="diamod-flex">
          <h4><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/zuanshi.png"/>当前钻石</h4>
          <div>{{diamodCount}}<small>个</small></div>
        </div>
        <a href="javascript:;" v-if="linkInfo" @click="goLink">立即使用</a>
      </div>
      <div class="diamond-warp">
        <div class="diamond-tab">
          <div :class="{'active': curType == item.type}" v-for="item in tabTypes" :key="item.id" @click="changeTab(item)">{{item.name}}</div>
        </div>
        <div class="diamond-empty" v-if="!lists.length && finished">
          <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/none.png" alt="">
          <p>暂无记录~</p>
        </div>
        <div class="diamond-list" v-else>
          <van-list
            v-model="loading"
            :finished="finished"
            @load="fetchData"
          >
            <div class="diamond-item" v-for="(item,index) in lists" :key="index" :class="{'active': item.show}">
              <div class="item-title" @click="toShow(item)">
                <h4>{{item.payTime | formatterStr}}</h4>
                <span>{{+item.transType ? '收入' :'支出'}}</span>
                <div>
                  <strong>{{+item.transType ? '+' :'-'}}{{item.changeCount}}</strong>
                  <p>{{item.remark}}</p>
                </div>
                <img class="arrow" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/arrow.png"/>
              </div>
              <div class="item-body">{{item.userSideMsg}}</div>
            </div>
          </van-list>
          <p class="load-end" v-if="finished">到底啦~</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import skeleton from '@/components/skeleton'
import URL from '@/http/url'
export default {
  name: 'diamond',
  components: {
    skeleton,
  },
  data() {
    return {
      skeletonLoading: true,
      diamodCount: 0, // 钻石数量
      loading: true,
      finished: false,
      curPage: 1,
      curType: null, // 当前tab
      tabTypes: [ // tabs
        { type: null, name: '全部', id: 0 },
        { type: 1, name: '收入', id: 1 },
        { type: 0, name: '支出', id: 2 },
      ],
      linkInfo: null, // 链接信息
      lists: []
    }
  },
  filters: {
    formatterStr(value) {
      return value.substring(0, 4) + '年' + value.substring(4, 6) + '月' + value.substring(6, 8) + '日'
    }
  },
  mounted() {
    if (this.$util.isUplus()) {
      this.$store.dispatch('handleUpAuth').then(res => {
        if (res) {
          this.loading = false;
          this.fetchTotal()
          this.fetchLink()
        } else {
          this.$store.dispatch('doLogin')
        }
      })
    } else {
      if (this.$store.state.isLogin) {
        this.loading = false
        this.fetchTotal()
        this.fetchLink()
      } else {
        this.$store.dispatch('doLogin')
      }
    }
  },
  methods: {
    fetchLink() {
      this.$http.get(URL.GET_DIAMOND_LINK).then(res => {
        let result = res.data;
        console.log(result, 'link')
        if (result.success) {
          this.linkInfo = result.data && result.data[0]
        }
      })
    },
    fetchTotal() {
      this.$http.get(URL.GET_DIAMOND_TOTAL).then(res => {
        let result = res.data;
        console.log(result, 'total')
        this.skeletonLoading = false
        if (result.success) {
          this.diamodCount = result.data.usableDiamond
        } else {
          this.$toast(result.message)
        }
      })
    },
    fetchData() {
      this.loading = true
      this.$http.get(URL.GET_DIAMOND_DETAIL, {
        params: {
          benefitType: 'diamond',
          transType: this.curType,
          pageNo: this.curPage,
          pageSize: 20
        }
      }).then(res => {
        let result = res.data;
        this.loading = false
        console.log(result)
        if (result.success) {
          if (result.data && result.data.paymentList) {
            let arr = result.data.paymentList.map(item => {
              return {
                ...item,
                show: false,
              }
            })
            this.lists = this.lists.concat(arr)
            this.curPage += 1;
            if (arr.length < 20) {
              this.finished = true
            }
          } else {
            this.finished = true
          }
        } else {
          this.$toast(result.message)
        }
      })
    },
    toShow(item) {
      item.show = !item.show
    },
    changeTab(item) {
      if (this.curType === item.type) return;
      this.curType = item.type;
      this.curPage = 1;
      this.lists = [];
      this.loading = false
      this.finished = false
      this.fetchData()
    },

    goLink() {
      console.log(this.linkInfo)
      if (this.linkInfo) {
        this.$store.dispatch('goPageByType', {
          params: this.linkInfo.hyperParam,
          type: +this.linkInfo.hyperType,
        })
      }
    }
  }
}
</script>

